import uImg from '@/components/zhtx-uploadImg/zhtx-uploadImg.vue';
let toast= msg=>{
	uni.showToast({
		title: msg,
		icon:'none'
	});
}

export default {
	components:{uImg},
	
	data() {
		return {
			msg:'',
			length:140,
			placeholder:'欢迎反馈任何意见和问题,您的反馈也是我们产品的动力哦',
			limitNum:9,
			uploadFileUrl: 'http://huahai.natapp1.cc/jeecg-boot/sys/common/upload', //替换成你的后端接收文件地址
			name:'', //上传的名字
			header: {// 如果需要header，请上传
			'content-type':'application/json'
			},
			uImgList1:[]
		}
	},
	computed:{
		computeLength(){
			return this.length=140-this.msg.length
		}
	},
	methods: {
		uploadSuccess(result) {
			if(result.statusCode == 200) {
				//上传成功的回调处理
				toast('上传成功')
			}else{
				toast('上传失败')
			}
		},
		upFile(){
			this.$refs.upimg.upload()
		}
	},

}
